var that;
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			navtitle: ['待付款', '拼团中', '拼团成功', '拼团失败', '售后'],
			list: [],
			paymodal: false,
			paylist: [
				{
					img: '/static/img/icon/icon-wxpay.png',
					name: '微信支付',
					id: '0',
					type: 'WECHAT_PAY'
				},
				{
					img: '/static/img/icon/icon-alpay.png',
					name: '支付宝支付',
					id: '0',
					type: 'AL_PAY'
				},
				{
					img: '/static/img/icon/iocn-lqpay.png',
					name: '零钱支付',
					id: '0',
					type: 'BALANCE_PAY'
				}
			],
			payaction: 1,
			nolist: false,
			nomore: false
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			that.getorder();
		},
		getorder() {
			that.nolist = false;
			this.$request.request({
				url: that.api.group.order.list,
				data: {
					status: that.TabCur
				},
				success(res) {
					that.list = res.list;
					if (res.list.length == 0) {
						that.nolist = true;
					}
				}
			});
		},
		tpinfo(e) {
			uni.navigateTo({
				url: '/pages/order/groupOrder/groupOrderInfo/groupOrderInfo?id=' + e.currentTarget.dataset.id
			});
		},
		toinfo(e) {
			console.log(e);
			if (e.currentTarget.dataset.type == 2) {
				uni.navigateTo({
					url: '/pages/order/groupOrder/groupOrderInfo/groupOrderInfo?id=' + e.currentTarget.dataset.id
				});
			} else {
				uni.navigateTo({
					url: '/pages/order/groupOrder/gorupgoodsinfo/gorupgoodsinfo?id=' + e.currentTarget.dataset.id
				});
			}
		},
		//取消订单
		orderRevoke: function(e) {
			var a = this;
			that.tanchuang
				.showModel({
					title: '提示',
					content: '是否取消该订单？',
					cancelText: '否',
					confirmText: '是'
				})
				.then(res => {
					if (res == 'confirm') {
						that.$request.request({
							url: 'api/order/revoke',
							data: {
								order_id: e.currentTarget.dataset.id
							},
							Toast: '正在执行',
							success(res) {
								console.log(res);
								that.$alertok(res.msg);
								that.getorder();
							}
						});
					}
				});
		}
	},
	onLoad(option) {
		that = this;
		if (option.id) {
			that.TabCur = option.id;
		}
	},
	onShow() {
		that.getorder();
	}
};
